* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.About_area {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background: black;
}
.back_color {
  background-color: rgba(0, 0, 0, 0.3);
  width: 100%;
  position: absolute;
  z-index: 2;
}

.all {
  margin-top: 50px;
  padding-bottom: 70px;
}
.about_text {
  text-align: center;
  margin: 0 auto;
  padding-top: 150px;
  font-size: 1vw;
  width: 60%;
}
.area {
  padding-top: 50px;  
  padding-bottom: 50px;
  border-radius: 50px;
  background-color: rgba(255, 255, 255, 0.8);
  font-weight: bold;
}
.Skill_text_area {
  text-align: center;
  margin-bottom: 50px;
}
.Skill_text {
  font-size: 50px;
  font-weight: bold;
  color: white;
}
.Skill_area {
  display: flex;
  margin: 0 auto;
  text-align: center;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
}
.skill_all {
  border: 2px solid rgb(112, 112, 112, 0.5);
  background-color: white;
  border-radius: 10px;
  margin: 15px;
  width: 30%;
  min-width: 100px;
  height: auto;
}
.skill_all > img {
  width: 100px;
  height: 100px;
  object-fit: cover;
}

.skill_all {
  transition: all 200ms cubic-bezier(0.39, 0.5, 0.15, 1.36);
  position: relative;
}

.skill_hover{
  width: 100%;
  height: 100%;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  border-radius: 10px;
}


span.about_type {
  color: rgb(255, 255, 255);
  font-weight: bold;
  font-size: 1.5vw;
}

@media screen and (max-width: 1500px) {
  .about_text {
    font-size: 1.5vw;
  }
  span.about_type {
    font-size: 1.5vw;
  }
}
@media screen and (max-width: 1000px) {
  .about_text {
    font-size: 1.7vw;
  }
  span.about_type {
    font-size: 1.7vw;
  }
}
@media screen and (max-width: 800px) {
  .about_text {
    padding-top: 400px;
    font-size: 1.9vw;
  }
  span.about_type {
    font-size: 1.9vw;
  }
  .skill_1 {
    background: url(./img/battery/12.svg) bottom;
  }
  .skill_2 {
    background: url(./img/battery/11.svg) bottom;
  }
  .skill_3 {
    background: url(./img/battery/10.svg) bottom;
  }
  .skill_4 {
    background: url(./img/battery/11.svg) bottom;
  }
  .skill_5 {
    background: url(./img/battery/9.svg) bottom;
  }
  .skill_6 {
    background: url(./img/battery/6.svg) bottom;
  }
  .skill_hover{
    opacity: 1;
    background-size: contain;
    background-repeat: no-repeat;
    transition: 0.6s;
    height: 150px;
  }
  .skill_all{
    margin-bottom: 70px;
  }

}

@media screen and (min-width:801px) {
.skill_1:hover {
  background: url(./img/battery/12.svg) center;
}
.skill_2:hover {
  background: url(./img/battery/11.svg) center;
}
.skill_3:hover {
  background: url(./img/battery/10.svg) center;
}
.skill_4:hover {
  background: url(./img/battery/11.svg) center;
}
.skill_5:hover {
  background: url(./img/battery/9.svg) center;
}
.skill_6:hover {
  background: url(./img/battery/6.svg) center;
}
.skill_hover:hover {
  opacity: 1;
  background-size: contain;
  background-repeat: no-repeat;
  transition: 0.6s;
}
}

.background_video {
  width: 100%;
  height: 100vh;
  object-fit: cover;
  position: fixed;
  top: 0;
  z-index: 1;
}