*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.GuestBookWrap{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: fixed;
    z-index: 2;
    color: white;
}

.GuestBookHeader{
    width: 70%;
    height: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 8px;
}

.GuestBookTable{
    width: 70%;
    height: 50vh;
    overflow: scroll;
}

.GuestBookTitle{
    font-size: 20px;
    font-weight: 700;
}

.GuestBookButton{
    font-size: 16px;
    padding: 4px 8px;
    border: 1px solid white;
    border-radius: 8px;
    cursor: pointer;
}

table{
    width: 100%;
    border-collapse: collapse;
    padding-top: 30px;
}

.TableContents{
    width: 100%;
}

.TableContents tr:hover{
    background: rgba(255, 255, 255, 0.1);
}

tr{
    width: 100%;
    height: 40px;
    font-size: 16px;
    font-weight: 500;
}

th, td{
    overflow:hidden;
    text-overflow:ellipsis;
    white-space:nowrap;
}

.TableName{
    width: 15%;
}

.TableTitle{
    width: 70%;
}

.TableDate{
    width: 15%;
}

/* 백그라운드 영상 */
.background_video {
    width: 100%;
    height: 100vh;
    object-fit: cover;
    position: fixed;
    top: 0;
    z-index: 1;
  }

  /* 글쓰기 버튼 */
  .writingBG{
    width: 100%;
    height: 100vh;
    position: fixed;
    z-index: 3;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .writingGround{
    background: white;
    width: 70%;
    padding: 16px;
    border-radius: 10px;
  }

  .writingGround div{
    padding: 8px 0;
  }

  .writingGround input, textarea{
    font-size: 18px;
    padding: 2px 5px;
  }

  .writingUserInfo{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .InfoInput{
    width: 45%;
  }

  #writingTitle{
    width: 100%;
  }

  #writingContents{
    width: 100%;
    height: 30vh;
  }

  .writingButtonArea{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .writingButton{
    padding: 4px 8px;
    margin: 0 16px;   
    font-size: 20px;
    font-weight: 700;
    cursor: pointer;
 }