.Main_area {
  width: 100%;
  min-height: 100vh;
  background-size: cover;
  font-family: "score5";
  background: black;
}
.back_color {
  width: 100%;
  position: absolute;
  z-index: 2;
}
.type_area {
  font-size: 3vw;
  width: 100%;
  padding-top: 190px;
  color: white;
  text-align: center;
  font-weight: bold;
}
.type_text {
  font-size: 2.2vw;
  display: flex;
  flex-direction: column;
}

.my_photo > img {
  width: 11.5%;
}

.hi_area {
  display: flex;
  justify-content: center;
}
.hi_imo {
  animation-name: icon-animation;
  animation-duration: 2.1s;
  animation-iteration-count: infinite;
  transform-origin: 70% 70%;
  display: inline-block;
}

@keyframes icon-animation {
  0% {
    transform: rotate(0deg);
  }
  10% {
    transform: rotate(-14deg);
  }
  20% {
    transform: rotate(-8deg);
  }
  30% {
    transform: rotate(14deg);
  }
  40% {
    transform: rotate(-4deg);
  }
  50% {
    transform: rotate(10deg);
  }
  60% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

@media screen and (max-width: 800px) {
  .type_area {
    padding-top: 270px;
    padding-bottom: 180px;
    font-size: 5vw;
  }
  .my_photo > img {
    width: 30%;
  }
  .type_text {
    font-size: 7vw;
  }
}

.background_video {
  width: 100%;
  height: 100vh;
  object-fit: cover;
  position: absolute;
  top: 0;
  z-index: 1;
}